body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 99vh;
  background: rgb(83, 83, 83);
  color: rgb(214, 214, 214);
}

.App {
  text-align: center;
  align-items: center;
  /* border: solid 1px rgb(255, 0, 0); */
  padding: 10px;
}

#main-box {
  height: 150px;
  width: 150px;
  margin: auto auto 20px auto;
  border: 5px solid rgb(0, 0, 0);
}

.costumize-area {
  margin: -5px;
  /* border: solid 1px rgb(255, 0, 0); */
}
span {
  color: rgba(255, 255, 255, 0.438);
}

#area {
  display: flex;
}

.custumize-section {
  background: rgb(51, 51, 51);
  box-shadow: rgba(0, 0, 0, 0.507) 0px 0px 10px;
  margin: 10px;
  padding: 10px;
}

input {
  max-width: 100px;
  padding: 5px;
  text-align: center;
  border: none;
  border-radius: 10px;
  background: rgb(27, 27, 27);
  color: #fff;
}

textarea {
  margin: 5px;
  padding: 10px;
  min-width: 300px;
  min-height: 90px;
  border: none;
  border-radius: 10px;
  background: rgb(27, 27, 27);
  color: #fff
}
textarea::selection {
  color: #000;
  background: #fff;
}

button {
  border: none;
  border-radius: 10px;
  padding: 10px;
  background: rgb(27, 27, 27);
  color: #fff;
  transition: .2s;
}
button:hover {
  background: rgba(27, 27, 27, 0.493);
  cursor: pointer;
}